import goodsItem from '@/components/goods-item.vue';
export default {
	data() {
		return {
			search: false,
			history:wx.getStorageSync('keywords')||[],
			keywords: '',
			goodsItems: [],
			pageNumber: 1, //分页相关
			pageSize: 6, //
			noMore: false,
			noData:false,
		};
	},
	components: {
		goodsItem
	},
	methods: {
		handleSearch() {
			this.search = true
			this.initLoad()
		},
		cancelSearch() {
			console.log('s')
			this.keywords = ''
			this.search = false
		},
		hotSearch(keywords){
			this.search = true
			this.keywords=keywords
				this.initLoad()
		},
		initLoad() {
			this.pageNumber = 1
			this.noMore = false
			noData:false
			this.goodsItems = []
			this.getData()
			if(this.keywords){
				let setArr=wx.getStorageSync('keywords')||[]
				setArr.unshift(this.keywords)
				setArr=[...new Set(setArr)]
				this.history=setArr
				wx.setStorageSync('keywords',setArr)
			}
			
		},
		getData() {
			if (this.noMore) {
				return
			}
			this.$getJson('/api/v3/jhs/list.jsp', {
				companyId: 202,
				pageNumber: this.pageNumber,
				search: this.keywords,
				pageSize: this.pageSize,
			}, 'POST', res => {
				const result = res.data || []
				if (this.pageNumber == 1) {
					this.goodsItems = result
					if(result.length===0){
						this.noData=true
					}else{
						this.noData=false
					}
				} else {
					this.goodsItems = [...this.goodsItems, ...result]
				}
				if (result.length < this.pageSize) {
					this.noMore = true
				}

				this.pageNumber = this.pageNumber + 1
			})

		},

	},
	//加载更多
	onReachBottom() {
		this.getData();
	},
}
